<template>
    <div id="pinfwei">
        <router-view></router-view>
        <div class="home-title">
            <img class="logo fl" src="../../assets/logo.jpg" alt="">
            <span class="title fl">
                二次运检精益管理系统
            </span>
            <span class="user-name fr">
                <span class="user-text"> admin,</span>
                <span>你好</span>
                <Button class="exit" type="primary" @click="exit">返回</Button>
            </span>
        </div>
        <div class="content222">
            <ul>
                <li class="xiaoshiid" v-for='item in xiaoshiList'>
                    <div class="cor" @click="toPinWeiMes">

                    </div>
                    <span>{{item.name}}</span>
                </li>
            </ul>
        </div>
        <div class="anniu">
            <Button type="warning" @click='addXiaoshi()'>新增小室</Button>
        </div>
        <div id="xiaoshi">
            <div class="rizhi-con">
                <Modal width="300" v-model="modal1" @on-ok="ok" @on-cancel="cancel" title="小室信息：">
                    <ul style='width: 100%;'>
                        <li style="height:34px;">
                            <div class="sel-name fl">小室名:</div>
                            <Input size="small" v-model="xiaoshiName" placeholder="" style="width: 130px" />
                        </li>
                        <li style="height:34px;">
                            <div class="sel-name fl">行 x 列:</div>
                            <Input size="small" v-model="hang" placeholder="" style="width:40px" />
                            <span style='margin: 0 13px'>x</span>
                            <Input size="small" v-model="lie" placeholder="" style="width:40px" />
                        </li>
                        <li style="height:34px;">
                            <div class="sel-name fl">方 &nbsp;&nbsp;&nbsp;位:</div>
                            <Input size="small" v-model="fangwei1" placeholder="" style="width: 40px" />
                            <span style='margin: 0 10px'>→</span>
                            <Input size="small" v-model="fangwei2" placeholder="" style="width: 40px" />
                        </li>
                    </ul>
                </Modal>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "pingweixinzeng",
        data() {
            return {
                modal1: false,
                xiaoshiName: "",
                hang: "",
                lie: "",
                fangwei1: "",
                fangwei2: "",
                xiaoshiList: [],
            }
        },
        components: {

        },
        methods: {
            exit() {
                this.$router.push("/shebei");
            },
            ok() {
                //储存缓存数据
                var _this = this;
                // 小室名字
                localStorage.setItem("xiaoshiName", this.xiaoshiName);
                //储存行
                localStorage.setItem("hang", this.hang);
                //储存列
                localStorage.setItem("lie", this.lie);
                _this.xiaoshiList.push({
                    name: _this.xiaoshiName,
                    id: '0000'
                })
            },
            cancel() {

            },
            addXiaoshi() {
                let _this = this;
                _this.modal1 = true;
            },
            toPinWeiMes() {
               this.$router.push('/pwxx');
            }
        }
    }
</script>

<style scoped lang='less'>
    #pinfwei {
        width: 100%;
        height: 100%;
    }

    .home-title {
        height: 80px;
        line-height: 80px;
        border-bottom: 1px solid #000;
        padding: 0 20px;
        .logo {
            height: 78px;
        }
    }

    .title {
        margin-left: 20px;
        font-size: 48px;
    }

    .user-name {
        font-size: 14px;
    }

    .user-text {
        color: #0000FF;
    }

    .exit {
        margin-left: 40px;
    }

    .anniu {
        position: absolute;
        bottom: 20px;
        left: 20px;
    }

    #xiaoshi .rizhi {
        position: absolute;
        right: 20px;
        top: 150px;
        height: 80px;
        width: 80px;
        padding-top: 22px;
        background-color: rgb(206, 202, 197);
        border-radius: 50%;
    }

    #xiaoshi .sel-name {
        width: 80px;
        height: 28px;
        line-height: 24px;
        margin-right: 30px;
    }

    .content222 {
        width: 90%;
        height: 480px;
        margin: 15px auto;
        overflow-y: auto;
    }

    .xiaoshiid {
        width: 16%;
        margin: 10px 18px;
        height: 160px;
        float: left;
    }

    .cor {
        width: 100%;
        height: 80%;
        border: 1px solid skyblue;
        margin-bottom: 10px;
    }
</style>